import React from 'react'
import { CartItemType } from '../App';
import './styles/Card.css'
import {allItems} from './back/Data'

type Props = { 
  item: CartItemType,
  addToCart:(clickedItem: CartItemType) => void 
}
const Card : React.FC<Props> = ({item, addToCart}) => {
  return (
    <div className="card">
        <div className='image'><img src='./pics/bread-basket.jpg'/></div>
        <div className='card-title'> {item.title}</div>
        <div className='card-price'> 
          <span>&#8377;</span> {item.price}
        </div>
        <button className='card-button' onClick={() => addToCart(item)}>Add to Cart </button>
    </div>
  )
}

export default Card