import NavBar from './components/NavBar';
import {BrowserRouter as Router, Route , Routes} from 'react-router-dom'
import Home from './components/Home';
import MyMap from './components/MyMap'
import Cart from './components/Cart';
import ShopCategory from './components/ShopCategory';
import { useState } from 'react';

export type CartItemType = {
  id: number;
  category: string;
  description: string;
  image: string;
  price: number;
  title: string;
  amount: number;
}

function App() {
  const [cartItems, setCartItems] = useState([] as CartItemType[])
  const getTotalItems = (items: CartItemType[]) => items.reduce((ack: number, item) => ack + item.amount,0);
  
  const handleAddToCart = (clickedItem: CartItemType) => {
    
    setCartItems (prev => {
      const isItemInCart =  prev.find(item => item.id === clickedItem.id)
      // console.log("InCart" + isItemInCart)
      if (isItemInCart) {
        return prev.map( item =>
        item.id === clickedItem.id ? { ...item, amount: item.amount +1} : item);
      }
      return [...prev, {...clickedItem, amount: 1}]
    }
    )
    // console.log("Cart:" )
    // cartItems.map((item)=> (
    //   console.log(item.title)
    // ))

  };

  const handleRemoveFromCart = () => null;
  const itemsNumber= getTotalItems(cartItems);

  return (
    
    <>
      <Router>
        <NavBar totalItems ={itemsNumber}/>
        <Routes>
          <Route path='/' element={<Home addToCart={handleAddToCart}/>}/>
          <Route path='/contact' element ={<div>Contact</div>}/>
          <Route path='/map' element={<MyMap/>}/>
          <Route path='/shoppingBag' element={<Cart 
                                              cartItems={cartItems}
                                              addToCart = {handleAddToCart}
                                              removeFromCart = {handleRemoveFromCart}
          />}/>
          {/* <Route path='/category/bread' element ={<ShopCategory cartItems={cartItems}
                                              addToCart = {handleAddToCart}
                                              removeFromCart = {handleRemoveFromCart}
                                              category="bread"/>}/>
          <Route path='/category/biscuits' element ={<ShopCategory cartItems={cartItems}
                                              addToCart = {handleAddToCart}
                                              removeFromCart = {handleRemoveFromCart}
                                              category="biscuits"/>}/>
          <Route path='/category/cakes' element ={<ShopCategory cartItems={cartItems}
                                              addToCart = {handleAddToCart}
                                              removeFromCart = {handleRemoveFromCart}
                                              category="cakes"/>}/>
          <Route path='/category/teaTimeTreats' element ={<ShopCategory cartItems={cartItems}
                                              addToCart = {handleAddToCart}
                                              removeFromCart = {handleRemoveFromCart}
                                              category="teaTimeTreats"/>}/> */}
        </Routes>
      </Router>
    </>
      
      
  );
}

export default App;
